<template>
  <div style="padding:20px;">
    <div class='main-container'>
      <Form :model='addData' ref='addForm' :label-width='120' label-position='left' :rules='rules'>
        <FormItem label="轮播名称：">
          <span>首页</span>
        </FormItem>
        <FormItem label="图片标题：" prop='title'>
          <Input v-model='addData.title' placeholder='请输入图片标题'></Input>
        </FormItem>
        <FormItem label="显示顺序：" prop='sortvalue'>
          <Row>
            <Col span='6'>
            <InputNumber v-model='addData.sortvalue' :min='0'></InputNumber>
            </Col>
            <Col span='8'>
            <span style="color:silver">数值越大，在轮播图的顺序越靠前</span>
            </Col>
          </Row>
        </FormItem>
        <FormItem label="跳转地址：" prop='linkurl'>
          <swiper-address v-model='addData.linkurl'></swiper-address>
        </FormItem>
        <FormItem label="图片：" prop='picurl'>
          <single-uploader :image='addData.picurl' @change='handleImgChange'></single-uploader>
        </FormItem>
        <FormItem>
          <Button type='primary' style='width:120px;height:36px;margin-right:30px' @click='handleSubmit' :loading='loading'>保存编辑</Button>
          <Button type='ghost' style='width:80px;height:36px;' @click='handleCancel'>取消</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
import swiperAddress from './SwiperAddress.vue'
import singleImgUploader from '../common/SingleImgUploader.vue'
export default {
  data() {
    return {
      swiperid: this.$route.query.id,
      addData: {
        sid:'',
        title: '',
        sortvalue: 0,
        linkurl: '',
        picurl: ''
      },
      rules: {
        title: [{ required: true, message: '标题名称不能为空！', trigger: 'blur' }],
        picurl: [{ required: true, message: '图片不能为空！', trigger: 'blur' }],
      },
      loading:false,
    }
  },
  mounted() {
    this.$sitemap.prvname = "轮播管理";
    this.$sitemap.currname = '编辑图片';
    this.getSwiperDetail(this.swiperid)
  },
  components: {
    'single-uploader': singleImgUploader,
    'swiper-address':swiperAddress,
  },
  methods: {
    handleImgChange(file) {
      this.addData.picurl = file.url
    },
    handleCancel() {
      this.$router.push('/swiper/list')
    },
    handleSubmit() {
      this.loading = true;
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          this.$http.post(this.$api.swiper_edit, this.addData).then(()=>{
            this.$Message.success('保存成功！')
            this.getSwiperDetail(this.swiperid)
          }).catch(erro => {
            console.log('请求失败', erro)
          }).then(()=>{
            this.loading = false
          })
        }
      })
    },
    getSwiperDetail(swiperid) {
      this.$http.post(this.$api.swiper_detail, {sid:swiperid}).then(res=>{
        this.addData=res.data.data
        // Object.keys(this.addData).forEach(key=>{
        //   if(key=='sid') {
        //     this.addData.sid = res.data.data.id
        //   }else {
        //     this.addData[key] = res.data.data[key]
        //   }

        // })
      }).catch(()=>{})
    }
  }
}

</script>
<style scoped>
.swiper-header {
  height: 50px;
  border-bottom: 1px solid #f0f0f0;
  font-size: 16px;
  margin-bottom: 20px;
  line-height: 50px;
  text-indent: 15px;
}

.main-container {
  padding: 0 20px;
}

.btn-wrapper {
  margin-top: 30px;
}

</style>



