<template>
  <div>
    <div class="swiper-show-value">
      <Select v-model="model.chosedType" style="width:200px" placeholder='选择跳转' clearable @on-change='hanlderType'>
        <Option :value='""'>无</Option>
        <Option v-for="type in typeArr" :value="type.value" :key="type.value" :disabled='type.disabled'>{{ type.label }}</Option>
      </Select>
      <div v-show='model.chosedType&&hasDetail[model.chosedType]'>
        <div v-if='model.chosedType=="category"' class='swiper-detail-items'>
          <span>已选择:</span>
          <Select v-model='model.chosedId' style="width:200px;margin-left:20px;" placeholder='选择具体商品分类' @on-change='handleCateChange'>
            <Option v-for="cate in cates" :value="cate.id" :key="cate.id">{{ cate.catename }}</Option>
          </Select>
        </div>
        <div v-show='showDetail' class='swiper-detail-items'>
          <div>已选择：{{model.chosedName}}</div>
          <Button type='ghost' style='margin-top:10px' @click='handleChoseDetail'>选择{{model.chosedType=='item'?'商品':'活动'}}</Button>
        </div>
      </div>
    </div>
    <Modal v-model='showModal' width='800' @on-ok='handleOk'>
      <div slot='header'>
        我的{{model.chosedType=='item'?'商品':'活动'}}
      </div>
      <div v-if='showDetail&&showModal'>
        <component :is="showWhich" class="tab" @chosedid-change='handleChosedId'></component>
      </div>
    </Modal>
  </div>
</template>
<script>
import itemSelect from './ItemSelect.vue'
import articleSelect from './ArticleSelect.vue'
export default {
  props: {
    value: {
      type: String,
      default:"",
    },
  },
  data() {
    return {
      showModal: false,
      model: {
 		chosedType: '',
      	chosedId: '',
      	chosedName: '',
      },
     detail:'',
      //有没有详情
      hasDetail: {
        item: true,
        category: true,
        article: true,
        aboutus: false,
        sales: true,
      },
      //类型控制
      typeArr: [{
        value: 'item',
        label: '商品详情',
        disabled: false,
      }, {
        value: 'category',
        label: '商品分类',
        disabled: false,
      }, {
        value: 'article',
        label: '活动详情',
        disabled: false,
      }, {
        value: 'aboutus',
        label: '公司介绍',
        disabled: false,
      }, {
        value: 'sales',
        label: '促销活动',
        disabled: true,
      }],
      //所有商品分类
      cates: [],
      //所有内容分类
      articletypes: [],
      temp:'',
    }
  },
  computed: {
    showDetail() {
      return this.model.chosedType == 'item' || this.model.chosedType == 'article'
    },
    showWhich() {
    	return this.model.chosedType == 'item'?itemSelect:articleSelect
    },
   
  },
  created() {
    this.getCategoryList();
  },
  watch:{
  	value(newval) {
  		let obj = this.value ? JSON.parse(this.value) : { type: '', id: '', name: '' };
  		this.detail = obj;
  		this.model.chosedType = obj.type;
  		this.model.chosedId = obj.id;
  		this.model.chosedName = obj.name;
  	},
  	
  },
  methods: {
  	hanlderType(val) {
  		if(val&&!this.hasDetail[val]){
  			this.$emit('input',JSON.stringify({type:this.model.chosedType,id:this.model.chosedId,name:this.model.chosedName}))
  		}
  	},
  	handleCateChange(val) {
  		
  		this.model.chosedName = this.cates.find(it=>it.id==val).catename;
  		this.$emit('input',JSON.stringify({type:this.model.chosedType,id:this.model.chosedId,name:this.model.chosedName}))
  	},
  	handleOk() {
  		this.model.chosedId = this.temp.chosedId;
  		this.model.chosedName = this.temp.chosedName;
  		this.$emit('input',JSON.stringify({type:this.model.chosedType,id:this.model.chosedId,name:this.model.chosedName}))
  	},
    handleChoseDetail() {
      this.showModal = true;
    },
    getCategoryList() {
      this.$http.post(this.$api.category_list, {}).then(res => {
        if (res.data.data) {
          this.cates = res.data.data
        }
      }).catch(erro => {
        console.log('请求cate列表接口', erro)
      })
    },
    getArticleType() {

    },
    handleChosedId(detail) {
    	this.temp = {
    		chosedId:detail.id||'',
    		chosedName:detail.title||''
    	}
    },
  },

}

</script>
<style>
.swiper-name {
  display: inline-block;
  margin-right: 10px;
}

.swiper-detail-items {
  margin: 20px 0;
}

</style>



