<template>
    <div>
        <div style='border-bottom:1px solid silver;padding-bottom:20px;'>
            <Row>
                <Col :span='12'>
                <Input placeholder='搜索活动标题' v-model.trim='queryForm.keyword' @input='handleInput'></Input>
                </Col>
            </Row>
        </div>
        <div style="position: relative;">
            <Spin size="large" fix v-show="spinShow"></Spin>
            <div class="article">
                <div v-for="v in articleList" :key="v.id" class="article-item" @click='handleChoseItem(v)' :class='{chosed:v.id==chosedItem.id}'>
                    <img :src='v.picture'/>
                    <div v-text="v.title"></div>
                </div>
            </div>
            <div class="clear-fix">
                <div class='footer-page'>
                    <Page :total='total' show-total  :page-size="queryForm.pagesize"  @on-change='indexChange'></Page>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      cates: [],
      timoutBol: false,
      total: 0,
      queryForm: {
        pageindex: 0,
        pagesize: 4,
        cateid: 1,
        keyword: ""
      },
      articleList: [],
      chosedItem: {},
      spinShow: false
    };
  },
  created() {
    this.getArticleList();
    this.getArticleTypes();
  },
  watch: {
    chosedItem(newVal) {
      this.$emit("chosedid-change", newVal);
    }
  },
  methods: {
    handleInput() {
      let that = this;
      if (!!that.timoutBol) {
        clearTimeout(that.timoutBol);
      }
      that.timoutBol = setTimeout(() => {
        that.getArticleList();
      }, 1000);
    },
    getArticleTypes() {
      this.$http
        .post(this.$api.articletype_list, {
          pageindex: 0,
          pagesize: 100
        })
        .then(res => {
          if (res.data.result) {
            this.cates = res.data.data;
          } else {
            this.$Message.error(res.data.msg);
          }
        });
    },
    indexChange(index) {
      this.queryForm.pageindex = index - 1;
      this.getArticleList();
    },
    formatParams() {
      let params = {};
      Object.keys(this.queryForm).forEach(key => {
        switch (key) {
          case "keyword":
            if (this.queryForm[key] !== "") {
              params[key] = this.queryForm[key];
            }
            break;
          default:
            params[key] = this.queryForm[key];
            break;
        }
      });
      return params;
    },
    getArticleList() {
      let params = this.formatParams();
      this.spinShow=true;
      console.log(params);
      this.$http.post(this.$api.article_list, params).then(res => {
        if (res.data.result) {
          this.articleList = res.data.data;
          this.total = +res.data.totalcount;
        } else {
          this.$Message.error(res.data.msg);
        }
      }).then(()=>{
          this.spinShow=false;
      });
    },
    handleChoseItem(item) {
      this.chosedItem = item;
    }
  }
};
</script>
<style lang="less" scoped>
.article {
  padding: 20px;
  .article-item {
    border: 1px solid #ccc;
    border-radius: 10px;
    margin-bottom: 10px;
    padding: 10px;
    img {
      width: 100px;
      height: 100px;
      vertical-align: middle;
    }
    div {
      padding-left: 10px;
      display: inline-block;
      vertical-align: middle;
      font-size: 16px;
    }
  }
  .article-item:hover,
  .article-item.chosed {
    background-color: #eee;
    border-color: #3bc0c3;
  }
}
.footer-page {
  float: right;
}
.clear-fix::after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
</style>



