<template>
  <div>
    <div style='border-bottom:1px solid silver;padding-bottom:20px;'>
      <Row>
        <Col :span='12'>
        <span>选择分类：</span>
        <Select v-model='cateid' style="width:200px;margin-left:20px;" placeholder='商品分类' clearable @on-change='handleCateChange'>
          <Option :value='0'>全部</Option>
          <Option v-for="cate in cates" :value="cate.id" :key="cate.id">{{ cate.catename }}</Option>
        </Select>
        </Col>
        <Col :span='8'>
        <Input placeholder='搜索商品名称' v-model='keyword' @input='handleInput'></Input>
        </Col>
      </Row>
    </div>
    <div style="position: relative;">
        <Spin size="large" fix v-show="spinShow"></Spin>
        <div class='swiper-item-list'>
            <div v-for='item in items' class="swiper-item-detail" :key='item.id' @click='handleChoseItem(item)' :class='{chosed:chosedItem.id==item.id}'>
                <img :src='item.picurl' class='pic'>
                <div class='texts'>
                    <div class='texts-title'>{{item.title}}</div>
                    <div>价格:￥ {{item.price}}</div>
                    <div>库存：{{item.num}}</div>
                </div>
            </div>
            <div style='clear:both'></div>
        </div>
        <div class='swiper-item-page'>
        <Page :total="totalcount" :page-size="pagesize" show-total @on-change='indexChange'></Page>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cates: [],
      cateid: 0,
      keyword: "",
      totalcount: 0,
      pagesize: 6,
      pageindex: 0,
      items: [],
      chosedItem: {},
      timoutBol: false,
      spinShow: false
    };
  },
  created() {
    console.log("商品");
    this.getCategoryList();
    this.getItemList();
  },
  methods: {
    handleInput() {
      let that = this;
      if (!!that.timoutBol) {
        clearTimeout(that.timoutBol);
      }
      that.timoutBol = setTimeout(() => {
        that.getItemList();
      }, 1000);
    },
    handleCateChange(cate) {
      this.getItemList();
    },
    handleChoseItem(item) {
      this.chosedItem = item;
    },
    getCategoryList() {
      this.$http
        .post(this.$api.category_list, {})
        .then(res => {
          if (res.data.data) {
            this.cates = res.data.data;
          }
        })
        .catch(erro => {
          console.log("请求cate列表接口", erro);
        });
    },
    getItemList() {
      this.spinShow=true;
      return this.$http
        .post(this.$api.item_list, this.conditions)
        .then(res => {
          if (res.data.result == "1") {
            this.items = res.data.data;
            this.totalcount = +res.data.totalcount;
          }
        })
        .catch(() => {}).then(()=>{
             this.spinShow=false;
        });
    },
    indexChange(index) {
      this.pageindex = index - 1;
      this.getItemList();
    }
  },
  watch: {
    chosedItem(newVal) {
      this.$emit("chosedid-change", newVal);
    }
  },
  computed: {
    conditions() {
      return {
        pageindex: this.pageindex || 0,
        pagesize: this.pagesize,
        keywords: this.keyword,
        cateid: this.cateid,
        ishidden: 0
      };
    }
  }
};
</script>
<style>
.swiper-item-page {
  text-align: right;
  padding: 10px;
}

.swiper-item-list {
  height: 400px;
  overflow: auto;
}
.swiper-item-detail {
  border: 1px solid #ddd;
  width: 240px;
  height: 120px;
  border-radius: 8px;
  box-sizing: border-box;
  padding: 10px 10px;
  float: left;
  margin: 30px 5px;
}
.swiper-item-detail:hover,
.swiper-item-detail.chosed {
  background-color: #eee;
  border-color: #3bc0c3;
}
.swiper-item-detail img {
  width: 100px;
  height: 100px;
  display: inline-block;
  vertical-align: top;
}
.swiper-item-detail .texts {
  width: 110px;
  height: 100px;
  display: inline-block;
}
.swiper-item-detail .texts div {
  width: 100%;
  height: 33px;
  line-height: 33px;
  font-size: 14px;
  text-align: center;
}
.swiper-item-detail .texts .texts-title {
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



